import React, { FC } from 'react';

import { useEditorContext } from 'state/editor';

import { ELEMENT, ElementUnion } from 'models';

import BlockComponent from './components/block/block';
import BorderComponent from './components/border/border';
import ImageComponent from './components/image/image';
import TableComponent from './components/table/table';

// import { VorlageComponent } from '../vorlage';

import './styles.css';

export interface EditorProps {}
const settingsComponents = (activeElement: ElementUnion, handleChangeSettings: any): any => ({
  [ELEMENT.BLOCK]: (
    <BlockComponent activeElement={activeElement} handleChangeSettings={handleChangeSettings} />
  ),
  [ELEMENT.BORDER]: (
    <BorderComponent activeElement={activeElement} handleChangeSettings={handleChangeSettings} />
  ),
  [ELEMENT.IMAGE]: (
    <ImageComponent activeElement={activeElement} handleChangeSettings={handleChangeSettings} />
  ),
  [ELEMENT.TABLE]: (
    <TableComponent activeElement={activeElement} handleChangeSettings={handleChangeSettings} />
  ),
});

const EditorComponent: FC<EditorProps> = () => {
  const { actions, activeId, activeElement, isDone } = useEditorContext();

  const handleChangeSettings = (style: string, value: string, type: string) => {
    actions.handleChangeSettings(
      activeId.page,
      activeId.id,
      style,
      value,
      type,
      activeElement as ElementUnion,
    );
  };

  return (
    <div className="editor-container">
      <div className="editor-page">
        Page settings
        {/*<>
          <Button icon="bg-colors" className="icon-settings" onClick={handleClick} />
          {displayColorPicker ? (
            <div style={popover as any}>
              <div style={cover as any} onClick={handleClose} />
              <ChromePicker
                color={colorActive}
                onChangeComplete={(color: any) =>
                  handleChangeSettings(colorActive.type, color.hex, 'style')
                }
              />
            </div>
          ) : null}
        </>*/}
      </div>

      {!isDone && !!activeElement && (
        <div className="editor-settings">
          {settingsComponents(activeElement, handleChangeSettings)[activeElement.type]}
        </div>
      )}
      {/* <VorlageComponent /> */}
    </div>
  );
};

export { EditorComponent };
