import React, { FC, useState } from 'react';
import { Link } from 'react-router-dom';

import { Icon, Layout, Menu } from 'antd';
import { ClickParam } from 'antd/lib/menu';

import { MODAL_MODES, useModalContext } from 'state';

import logo from 'assets/logo.png';
import './styles.css';

export interface MenuItem {
  key: string;
  text: string;
  icon: string;
}
export interface SidebarProps {}

const SidebarComponent: FC<SidebarProps> = props => {
  const [collapsed, setCollapsed] = useState(false);
  const { modalToggle } = useModalContext();

  const onMenuClick = (params: ClickParam) => {
    if (params.key === 'file') {
      modalToggle(MODAL_MODES.CREATE_DOCUMENT);
    }

    if (params.key === 'folder') {
      modalToggle(MODAL_MODES.CREATE_FOLDER);
    }
  };

  const menuItems: MenuItem[] = [
    {
      key: 'folder',
      text: 'New folder',
      icon: 'folder-add',
    },
    {
      key: 'file',
      text: 'New document',
      icon: 'file-add',
    },
  ];

  return (
    <Layout.Sider collapsible={true} collapsed={collapsed} onCollapse={setCollapsed}>
      <Link to="/" className="logoContainer">
        <img className="logo" alt="logo" src={logo} />

        {'binale'.split('').map((letter, index) => (
          <span key={index} className="logoText">
            {letter}
          </span>
        ))}
      </Link>

      <Menu theme="dark" selectable={false} onClick={onMenuClick}>
        {menuItems.map(item => (
          <Menu.Item key={item.key}>
            <Icon type={item.icon} />
            <span>{item.text}</span>
          </Menu.Item>
        ))}
      </Menu>
    </Layout.Sider>
  );
};

export { SidebarComponent };
