import React, { FC } from 'react';

import { ImageProps } from 'models';

import './styles.css';

interface ImageViewProps extends ImageProps {
  value?: string;
}

const ImageView: FC<ImageViewProps> = props => {
  const { value } = props;

  return (
    <div
      style={{
        width: '100%',
        height: '100%',
        background: `url(${value}) no-repeat`,
        backgroundSize: '100% 100%',
      }}
    />
  );
};

export { ImageView };
