import React, { FC } from 'react';
import { RouteComponentProps } from 'react-router-dom';

import Icon from 'antd/lib/icon';
import Table from 'antd/lib/table';

import { ContextMenuTrigger } from 'react-contextmenu';
import { CONTEXT_MENU_ID, getContextMenuId, menus } from './contextMenu';

import { ContextMenuComponent } from 'components/contextMenu';
import { DialogComponent } from 'components/dialog';
import { TreeComponent } from 'components/fsTree';
import { Modal } from 'components/modal';

import { FILE_STATUS, FILE_SYSTEM_ELEMENT, FileSystemElement, FSTarget } from 'models';

import classNames from 'utils/classnames';

import { useFileSystem, useModal } from 'hooks';
import { MODAL_MODES, useModalContext } from 'state';

import 'antd/es/tree/style/css';
import './styles.css';

export interface IconsMap {
  [key: string]: JSX.Element;
}

const getIcon = (item: FileSystemElement) => {
  let docMark = 'sync';
  if (item.status === FILE_STATUS.TEMPLATE) docMark = 'layout';
  if (item.status === FILE_STATUS.DOCUMENT__DONE) docMark = 'check-circle';

  let folderType = 'lock';
  if (item.name === 'Vorlage') folderType = 'layout';

  const iconsMap: IconsMap = {
    [FILE_SYSTEM_ELEMENT.FOLDER]: (
      <>
        {item.isDefault && <Icon className="marker" type={folderType} />}
        <Icon className="icon folder" type="folder" theme="filled" />
      </>
    ),
    [FILE_SYSTEM_ELEMENT.DOCUMENT]: (
      <>
        {<Icon className="marker doc" type={docMark} />}
        <Icon className="icon" type="file" theme="filled" />
      </>
    ),
  };

  return iconsMap[item.type];
};

const getFSElementClasses = (item: FileSystemElement): string => {
  const classes = {
    'fs-element': true,
    'in-progress': item.status === FILE_STATUS.DOCUMENT_IN_PROGRESS,
  };

  return classNames(classes);
};

export interface ExplorerProps extends RouteComponentProps<{ id: string }> {}

const ExplorerComponent: FC<ExplorerProps> = props => {
  const { match, history } = props;

  const {
    items,
    elements,
    copiedElement,
    parentForCreateFromTemplate,
    ...fsActions
  } = useFileSystem(match.params.id);
  const [elementsModal, elementsModalActions] = useModal<FSTarget<FileSystemElement>>();
  const { modalToggle } = useModalContext();

  const handleFSElement = (item: FileSystemElement) => () => {
    if (item.type === FILE_SYSTEM_ELEMENT.FOLDER) {
      history.push({ pathname: `${item.id}` });
    } else {
      if (item.status === FILE_STATUS.TEMPLATE) {
        const target: FSTarget<FileSystemElement> = {
          item,
          name: 'new_file_from_template',
          value: 'new_file_from_template',
        };

        elementsModalActions.setModalState({
          open: true,
          title: 'Create document from template',
          target,
          content: (
            <TreeComponent
              data={fsActions.getFolderElementsTreeById('Root', FILE_SYSTEM_ELEMENT.FOLDER)}
              onSelect={fsActions.setParentForCreateFromTemplate}
            />
          ),
        });
      } else {
        history.push(`/document/${item.id}`);
      }
    }
  };

  const handleContextMenuActions = (event: any, target: FSTarget<FileSystemElement>) => {
    switch (target.value) {
      case 'new_file': {
        return modalToggle(MODAL_MODES.CREATE_DOCUMENT);
      }
      case 'new_folder': {
        return modalToggle(MODAL_MODES.CREATE_FOLDER);
      }
      case 'rename': {
        return modalToggle(MODAL_MODES.RENAME_FS_ITEM, target.item);
      }
      case 'edit-template': {
        return history.push(`/document/${target.item.id}`);
      }
      case 'past': {
        return fsActions.pasteItem(match.params.id);
      }
      case 'delete': {
        if (target.item.isDefault) return;
        const title = 'Are you sure you want to delete?';
        return elementsModalActions.setModalState({ open: true, title, target });
      }
      case 'download': {
        return console.log('download', event, target);
      }
      case 'info': {
        const title = 'Document information';

        const columns = [
          {
            dataIndex: 'title',
          },
          {
            dataIndex: 'date',
          },
        ];

        const data = [
          {
            title: 'Created',
            date: '19.02.2019',
          },
          {
            title: 'Updated',
            date: '19.02.2019',
          },
        ];

        const content = (
          <Table
            size="small"
            bordered={true}
            showHeader={false}
            pagination={false}
            columns={columns}
            dataSource={data}
          />
        );

        return elementsModalActions.setModalState({ open: true, title, target, content });
      }
      case 'copy': {
        return fsActions.copyItem(target.item);
      }
      case 'commit': {
        const title = 'Are you sure you want to commit?';
        return elementsModalActions.setModalState({ open: true, title, target });
      }
      default:
        return;
    }
  };

  const handleElementsModalAction = async () => {
    if (!elementsModal.target) return;

    const { value, name, item } = elementsModal.target;

    if (!value || !item) return;

    try {
      if (value === 'delete') {
        await fsActions.removeById(item.id);
      }
      if (value === 'commit') {
        await fsActions.commit(item.id);
      }
      if (value === 'new_file_from_template') {
        modalToggle(MODAL_MODES.CREATE_DOCUMENT_FROM_TEMPLATE, item);
      }

      elementsModalActions.closeModal();
    } catch (error) {
      console.log(`Error on ${name} FS element`, error);
    }
  };

  const getElements = (data: FileSystemElement[], index: number) => (
    <div key={index} className="root">
      {data.map(item => (
        <ContextMenuTrigger
          key={item.id}
          collect={params => ({ ...params, item })}
          id={item.isDefault ? '' : getContextMenuId(item.type)}
        >
          <div className={getFSElementClasses(item)} onClick={handleFSElement(item)}>
            {getIcon(item)}
            <span className="text">{item.name}</span>
          </div>
        </ContextMenuTrigger>
      ))}
    </div>
  );

  return (
    <ContextMenuTrigger id={CONTEXT_MENU_ID.EXPLORER}>
      <div className="explorer">{elements.map(getElements)}</div>

      {menus.map(item => {
        let menuData = item.data;

        const parent = fsActions.getElement(match.params.id);
        const isTemplate: boolean = !!parent && parent.name === 'Vorlage';

        if (item.currentId === CONTEXT_MENU_ID.EXPLORER) {
          menuData = item.data.filter(
            i =>
              i.value !== 'past' ||
              (copiedElement &&
                ((copiedElement.status !== FILE_STATUS.TEMPLATE && !isTemplate) ||
                  (copiedElement.status === FILE_STATUS.TEMPLATE && isTemplate))),
          );
        }

        return (
          <ContextMenuComponent
            key={item.currentId}
            data={menuData}
            currentId={item.currentId}
            onChange={handleContextMenuActions}
          />
        );
      })}

      <Modal />

      <DialogComponent
        open={elementsModal.open}
        title={elementsModal.title}
        content={elementsModal.content}
        onAction={handleElementsModalAction}
        onClose={elementsModalActions.closeModal}
      />
    </ContextMenuTrigger>
  );
};

export { ExplorerComponent };
