import { useContext, useEffect, useState } from 'react';

import { elements } from 'components/elements';

import { FSContext } from 'state/context';

import {
  Document,
  DocumentPage,
  ElementType,
  ElementUnion,
  FILE_STATUS,
  FileSystemElement,
  Position,
  TableElement,
  TextElement,
} from 'models';

import { getIdFromCellPosition } from 'components/elements/table/helpers';
import { deepClone } from 'utils';

const maxX: number = 595.28;
const maxY: number = 841.89;

const removeCallback = (removeIndex: number) => (_: any, index: number) => index !== removeIndex;

interface EditorState {
  document: FileSystemElement | null;
  activeElement: ElementUnion | null;
  pages: DocumentPage[];
  isDone: boolean;
  showPreview: boolean;
  actions: EditorActions;
  activeId: any;
  drawerVisible: boolean;
  preview: boolean;
}

export interface EditorActions {
  addPage: () => void;
  showPreviewHandler: () => void;
  deletePage: (pageIndex: number) => void;
  addElement: (pageIndex: number, type: ElementType, position: Position, value?: string) => void;
  changeElement: (pageIndex: number, elementIndex: number, element: ElementUnion) => void;
  deleteElement: (pageIndex: number, elementIndex: number) => void;
  setActiveElement: (element: ElementUnion) => void;
  resetActiveElement: () => void;
  isElementActive: (element: ElementUnion) => boolean;
  addRowToTableElement: (pageIndex: number, elementIndex: number, element: TableElement) => void;
  addColumnToTableElement: (pageIndex: number, elementIndex: number, element: TableElement) => void;
  addColSpan: (
    pageIndex: number,
    elementIndex: number,
    element: TableElement,
    row: number,
    column: number,
  ) => void;
  removeRowFromTableElement: (
    pageIndex: number,
    elementIndex: number,
    element: TableElement,
    row: number,
  ) => void;
  removeColumnFromTableElement: (
    pageIndex: number,
    elementIndex: number,
    element: TableElement,
    column: number,
  ) => void;
  togglePreview: () => void;
  closeDrawer: () => void;
  showDrawer: () => void;
  addImage: (pageIndex: number, type: ElementType, position: Position) => void;
  handleChangeText: (
    pageIndex: number,
    elementIndex: number,
    value: string,
    element: TextElement,
  ) => void;
  changeTextStyle: (
    pageIndex: number,
    elementIndex: number,
    style: string,
    value: string,
    element: TextElement,
  ) => void;
  handleChangeSettings: (
    pageIndex: number,
    elementIndex: number,
    style: string,
    value: string,
    type: string,
    element: ElementUnion,
  ) => void;
  setActiveId: (page: number, id: number) => void;
  resetActiveId: () => void;
}

export interface EditorProps {
  fsElementId: string | undefined;
}

let saveIntervalId: any;

const pagesKey = Symbol('pages');

export function useEditor(props: EditorProps): EditorState {
  const { getElement, documents } = useContext(FSContext);
  const fsElement = getElement(props.fsElementId);

  const isDone = !!fsElement && fsElement.status === FILE_STATUS.DOCUMENT__DONE;

  const [pages, setPages] = useState<DocumentPage[]>([[]]);

  // FIXME: solve LexicalEnvironment issue with refs
  (window as any)[pagesKey] = pages;

  useEffect(() => {
    if (!fsElement) return;

    const unsubscribe = documents.doc(fsElement.documentId).onSnapshot(querySnapshot => {
      console.log('subscribe');
      const document = querySnapshot.data() as Document;
      const content: DocumentPage[] = JSON.parse(document.content);
      if (Array.isArray(content)) setPages(content);
    });

    saveIntervalId = setInterval(() => {
      const content = JSON.stringify((window as any)[pagesKey]);
      documents.doc(fsElement.documentId).update({ content });
      console.log('Document was updated');
    }, 5000);

    return () => {
      unsubscribe();
      clearInterval(saveIntervalId);
      console.log('unsubscribe');
    };
  }, [fsElement, documents]);

  const addPage = () => setPages([...pages, []]);

  const deletePage = (pageIndex: number) => {
    setPages(pages.filter(removeCallback(pageIndex)));
  };

  // tslint:disable-next-line: variable-name
  const [activeElement, _setActiveElement] = useState<ElementUnion | null>(null);

  const resetActiveElement = () => _setActiveElement(null);
  const isElementActive = (element: ElementUnion): boolean => {
    return !!activeElement && activeElement.id === element.id;
  };
  const setActiveElement = (element: ElementUnion) => {
    if (isElementActive(element)) {
      resetActiveElement();
    } else {
      _setActiveElement(element);
    }
  };
  // tslint:disable-next-line: variable-name
  const [activeId, _setActiveId] = useState<{ page: number; id: number } | null>(null);

  const setActiveId = (page: number, id: number) => _setActiveId({ page, id });
  const resetActiveId = () => _setActiveId(null);

  const addElement = (pageIndex: number, type: ElementType, position: Position, value?: string) => {
    const element = deepClone<ElementUnion>(elements[type].element);
    if (!element) throw new Error('Unknown element type: ' + type);
    const zoom: any = (window.screen.pixelDepth / 10) * 2;
    const { size } = element;
    const x = maxX < position.x + size.width ? maxX - size.width : position.x / zoom;
    const y = maxY < position.y + size.height ? maxY - size.height : position.y / zoom;
    element.position = { x, y };
    element.id = `f${(+new Date()).toString(16)}`;
    if (value && 'value' in element) {
      element.value = value;
    }

    const page: DocumentPage = pages[pageIndex];
    pages[pageIndex] = [...page, element];
    setPages([...pages]);
  };

  const deleteElement = (pageIndex: number, elementIndex: number) => {
    pages[pageIndex] = pages[pageIndex].filter(removeCallback(elementIndex));
    setPages([...pages]);
  };

  const changeElement = (pageIndex: number, elementIndex: number, element: ElementUnion) => {
    pages[pageIndex][elementIndex] = { ...element } as ElementUnion;
    setPages([...pages]);
  };

  const addRowToTableElement = (pageIndex: number, elementIndex: number, element: TableElement) => {
    const { data, hasFooter } = element;
    const insertIndex: number = data.length - (hasFooter ? 1 : 0);
    const columns = data[insertIndex].map(_ => 'cell');
    element.data.splice(insertIndex, 0, columns);
    changeElement(pageIndex, elementIndex, element);
  };

  const addColumnToTableElement = (
    pageIndex: number,
    elementIndex: number,
    element: TableElement,
  ) => {
    const { data } = element;
    element.data = data.map(page => [...page, 'cell']);
    changeElement(pageIndex, elementIndex, element);
  };

  const addColSpan = (
    pageIndex: number,
    elementIndex: number,
    element: TableElement,
    row: number,
    column: number,
  ) => {
    if (!element.cellsStyle) {
      throw new Error('Error: Table has not cell styles');
    }

    const data = deepClone(element.data);
    data[row].splice(column + 1, 1);

    const cellId = getIdFromCellPosition({ row, column });
    element.cellsStyle[cellId] = element.cellsStyle[cellId] || {};
    const prevColspan = element.cellsStyle[cellId].colSpan || 1;

    element.cellsStyle[cellId] = {
      colSpan: prevColspan + 1,
      style: {
        ...element.cellsStyle[cellId].style,
      },
    };

    element.data = data;

    changeElement(pageIndex, elementIndex, element);
  };

  const removeRowFromTableElement = (
    pageIndex: number,
    elementIndex: number,
    element: TableElement,
    row: number,
  ) => {
    const data = deepClone(element.data);
    data.splice(row, 1);
    element.data = data;
    changeElement(pageIndex, elementIndex, element);
  };

  const removeColumnFromTableElement = (
    pageIndex: number,
    elementIndex: number,
    element: TableElement,
    column: number,
  ) => {
    const data = deepClone(element.data);
    data.forEach(row => row.splice(column, 1));
    element.data = data;
    changeElement(pageIndex, elementIndex, element);
  };

  const handleChangeText = (
    pageIndex: number,
    elementIndex: number,
    value: string,
    element: TextElement,
  ) => {
    element.value = value;
    changeElement(pageIndex, elementIndex, element);
  };

  const changeTextStyle = (
    pageIndex: number,
    elementIndex: number,
    style: string,
    value: string,
    element: any,
  ) => {
    if (element.style[style] === 'underline') {
      value = 'none';
    }
    if (element.style[style] === 'italic') {
      value = 'normal';
    }
    if (element.style[style] === 'bold') {
      value = 'normal';
    }
    if (style === 'fontSize') {
      value = `${value}pt`;
    }

    element.style[style] = value;
    changeElement(pageIndex, elementIndex, element);
  };

  const handleChangeSettings = (
    pageIndex: number,
    elementIndex: number,
    style: string,
    value: string,
    type: string,
  ) => {
    const element: any = deepClone(activeElement);
    element[type][style] = value;
    changeElement(pageIndex, elementIndex, element);
  };

  const addImage = (pageIndex: number, type: ElementType, position: Position) => {
    const input = document.createElement('input');
    input.type = 'file';

    let file: any = {};
    let result: string = '';

    input.onchange = (e: any) => {
      const reader = new FileReader();

      file = e.target.files[0];
      // tslint:disable-next-line:only-arrow-functions
      reader.onload = function(a: any) {
        result = a.target.result;
        addElement(pageIndex, type, position, result);
      };
      reader.readAsDataURL(file);
    };
    input.click();
  };

  const [showPreview, setShowPreview] = useState(false);

  const togglePreview = () => setShowPreview(!showPreview);

  const [drawerVisible, setDrawerVisible] = useState(false);
  const showDrawer = () => setDrawerVisible(true);
  const closeDrawer = () => setDrawerVisible(false);

  const [preview, setPreview] = useState(false);
  const showPreviewHandler = () => setPreview(!preview);

  return {
    document: fsElement,
    activeId,
    activeElement,
    pages,
    isDone,
    preview,
    showPreview,
    drawerVisible,
    actions: {
      showPreviewHandler,
      addPage,
      deletePage,
      addElement,
      changeElement,
      deleteElement,
      setActiveElement,
      resetActiveElement,
      isElementActive,
      addRowToTableElement,
      addColumnToTableElement,
      addColSpan,
      removeRowFromTableElement,
      removeColumnFromTableElement,
      togglePreview,
      handleChangeText,
      changeTextStyle,
      setActiveId,
      resetActiveId,
      handleChangeSettings,
      addImage,
      showDrawer,
      closeDrawer,
    },
  };
}
