import React, { FC } from 'react';

import { FileSystemElement, FileSystemNode } from 'models';

import Icon from 'antd/lib/icon';
import Tree, { AntdTreeNodeAttribute } from 'antd/lib/tree';

const getTreeNodeIcon = ({ expanded }: AntdTreeNodeAttribute) => (
  <Icon type={expanded ? 'folder-open' : 'folder'} theme="filled" />
);

const getTreeNodes = (data: FileSystemNode[]) => {
  return data.map(item => (
    <Tree.TreeNode data={item} key={item.id} icon={getTreeNodeIcon} title={item.name}>
      {item.children ? getTreeNodes(item.children) : null}
    </Tree.TreeNode>
  ));
};

export interface TreeComponentProps {
  data: FileSystemNode[];
  onSelect: (parent: FileSystemElement) => void;
}

export const TreeComponent: FC<TreeComponentProps> = props => {
  const { data, onSelect } = props;

  return (
    <Tree
      showIcon={true}
      switcherIcon={<Icon type="down" />}
      onSelect={(_, event) => {
        if (!event.node.props.data) return;
        onSelect(event.node.props.data);
      }}
    >
      {getTreeNodes(data)}
    </Tree>
  );
};
