import React, { FC } from 'react';

import { useDrag } from 'react-dnd';

import { Icon } from 'antd';

import { Control } from 'models';

import './styles.css';

export const DND_CONTROL_TYPES = {
  BOX: 'DND_CONTROL_TYPE__BOX',
};

export interface ElementProps {
  control: Control;
}

const ElementControlComponent: FC<ElementProps> = props => {
  const { control } = props;
  const [, drag] = useDrag({ item: { type: DND_CONTROL_TYPES.BOX, element: control.type } });
  return (
    <div>
      <div className="element-control" ref={drag}>
        <Icon type={control.icon} />
        <span className="text">{control.name}</span>
      </div>
    </div>
  );
};

export { ElementControlComponent };
