import React, { FC } from 'react';
import { RouteComponentProps } from 'react-router-dom';

import { DocumentPageComponent } from './document';

import { useEditorContext } from 'state/editor';

interface DocumentPage extends RouteComponentProps<{ id: string }> {}

const DocumentPage: FC<DocumentPage> = props => {
  const {
    match: { params },
  } = props;

  return (
    <useEditorContext.Provider fsElementId={params.id}>
      <DocumentPageComponent />
    </useEditorContext.Provider>
  );
};

export { DocumentPage };
