import { Button, Icon, InputNumber } from 'antd';
import React, { FC, useState } from 'react';
import { ChromePicker } from 'react-color';

import './styles.css';

export interface TextSettingsProps {
  changeTextStyle: (style: string, value: string) => void;
  element: any;
}

const settingsContent = [
  {
    icon: 'align-left',
    style: 'textAlign',
    value: 'left',
  },
  {
    icon: 'align-center',
    style: 'textAlign',
    value: 'center',
  },
  {
    icon: 'align-right',
    style: 'textAlign',
    value: 'right',
  },
  {
    icon: 'underline',
    style: 'textDecoration',
    value: 'underline',
  },
  {
    icon: 'bold',
    style: 'fontWeight',
    value: 'bold',
  },
  {
    icon: 'italic',
    style: 'fontWeight',
    value: 'italic',
  },
];

const popover = {
  position: 'absolute',
  zIndex: '2',
};
const cover = {
  position: 'fixed',
  top: '0px',
  right: '0px',
  bottom: '0px',
  left: '0px',
};

const TextSettings: FC<TextSettingsProps> = ({ changeTextStyle, element }) => {
  const { style } = element;
  const [displayColorPicker, setDisplayColorPicker] = useState(false);

  const handleClick = () => {
    setDisplayColorPicker(!displayColorPicker);
  };

  const handleClose = () => {
    setDisplayColorPicker(false);
  };

  return (
    <div className="text-settings-root">
      <div className="font-setting">
        <Icon type="font-size" className="icon-settings" />
        <InputNumber value={style.fontSize} onChange={(e: any) => changeTextStyle('fontSize', e)} />
      </div>
      <Button icon="font-colors" className="icon-settings" onClick={handleClick} />
      {displayColorPicker ? (
        <div style={popover as any}>
          <div style={cover as any} onClick={handleClose} />
          <ChromePicker
            color={style.color}
            onChangeComplete={(color: any) => changeTextStyle('color', color.hex)}
          />
        </div>
      ) : null}
      {settingsContent.map((item, index) => (
        <Button
          icon={item.icon}
          key={index}
          className="icon-settings"
          onClick={() => changeTextStyle(item.style, item.value)}
        />
      ))}
    </div>
  );
};

export { TextSettings };
