import { useState } from 'react';

export type ModalState<T> = {
  open: boolean;
  title?: string;
  target?: T;
  content?: any;
};

export interface ModalActions<T> {
  setModalState: (nextState: ModalState<T>) => void;
  closeModal: () => void;
}

const initialState: ModalState<any> = {
  open: false,
  title: '',
  target: null,
  content: null,
};

export function useModal<T>(): [ModalState<T>, ModalActions<T>] {
  const [state, setState] = useState<ModalState<T>>(initialState);

  const setModalState = (nextState: ModalState<T>) => {
    setState(nextState);
  };

  const closeModal = () => {
    setState({ ...initialState });
  };

  return [
    state,
    {
      setModalState,
      closeModal,
    },
  ];
}
