import React, { FC } from 'react';
import { HashRouter, Redirect, Route, RouteComponentProps, Switch } from 'react-router-dom';

import { DashboardPage } from 'pages/dashboard';
import { DocumentPage } from 'pages/document';

function renderLayout(props: RouteComponentProps<any, any>) {
  // TODO: implement auth if it'll be needed
  const loggedIn: boolean = true;

  if (loggedIn) {
    return <Redirect to="/dashboard/Root" />;
  } else {
    return <Redirect to="/auth" />;
  }
}

export const Router: FC = () => (
  <HashRouter>
    <Route exact={true} path="/" render={renderLayout} />
    <Switch>
      <Route path="/dashboard/:id" component={DashboardPage} />
      <Route path="/document/:id" component={DocumentPage} />
    </Switch>
  </HashRouter>
);
