import { Input } from 'antd';
import { TextProps } from 'models';
import React, { CSSProperties, FC } from 'react';
import './styles.css';

const { TextArea } = Input;

interface TextViewProps extends TextProps {
  isActive: boolean;
  value?: string;
  handleChangeText: (value: string) => void;
}

const TextView: FC<TextViewProps> = props => {
  const { style, isActive, handleChangeText, value } = props;
  return (
    <div className="text-element">
      {isActive ? (
        <TextArea
          autosize={true}
          value={value}
          style={{ ...(style as CSSProperties), minHeight: '100%' }}
          onChange={(e: any) => handleChangeText(e.target.value)}
        />
      ) : (
        <div style={{ ...(style as CSSProperties) }}>{value}</div>
      )}
    </div>
  );
};

export { TextView };
