import createUseContext from 'constate';
import { useState } from 'react';

const CREATE_FOLDER = 'CREATE_FOLDER';
const CREATE_DOCUMENT = 'CREATE_DOCUMENT';
const CREATE_DOCUMENT_FROM_TEMPLATE = 'CREATE_DOCUMENT_FROM_TEMPLATE';
const RENAME_FS_ITEM = 'RENAME_FS_ITEM';
export const MODAL_MODES = {
  CREATE_FOLDER,
  CREATE_DOCUMENT,
  CREATE_DOCUMENT_FROM_TEMPLATE,
  RENAME_FS_ITEM,
};

export type ModalModesType =
  | typeof MODAL_MODES.CREATE_FOLDER
  | typeof MODAL_MODES.CREATE_DOCUMENT
  | typeof MODAL_MODES.CREATE_DOCUMENT_FROM_TEMPLATE
  | typeof MODAL_MODES.RENAME_FS_ITEM;

function useModal() {
  const [open, setOpen] = useState(false);
  const [mode, setMode] = useState<ModalModesType>(MODAL_MODES.CREATE_FOLDER);
  const [target, setTarget] = useState<any>(null);

  // tslint:disable-next-line: no-shadowed-variable
  const modalToggle = (modalMode?: ModalModesType, target?: any) => {
    setOpen(!open);
    setMode(modalMode || MODAL_MODES.CREATE_FOLDER);
    setTarget(target);
  };

  return {
    open,
    mode,
    modalToggle,
    target,
    setTarget,
  };
}

export const useModalContext = createUseContext(useModal);
