import React, { FC } from 'react';

import { StyleSheet, Text, View } from '@react-pdf/renderer';

import { CellProps, TableProps } from 'models';

import { baseTableStyles, cellRender, getTableMembers } from './helpers';

const defaultStyles: any = StyleSheet.create(baseTableStyles);

const TableCell: FC<CellProps> = props => {
  const { value, style } = props;

  return (
    <View style={style}>
      {/* <Text style={{ textWrap: 'nowrap', textOverflow: 'ellipsis', maxLines: 1 }}>{value}</Text> */}
      <Text>{value}</Text>
    </View>
  );
};

const TablePDF: FC<TableProps> = props => {
  const { data, hasFooter = false, style = {}, cellsStyle = {} } = props;
  const { header, body, footer } = getTableMembers(data, hasFooter);

  const defaultColumnWidth = 100 / header.length;
  const height = data.length;

  const render = cellRender(
    defaultColumnWidth,
    {
      ...defaultStyles.cell,
      ...{
        backgroundColor: style.backgroundColor,
        borderWidth: style.borderWidth,
        borderStyle: style.borderStyle,
        borderColor: style.borderColor,
      },
    },
    cellsStyle,
    TableCell,
  );

  return (
    <View style={{ ...defaultStyles.table, ...style }}>
      <View style={defaultStyles.header}>{header.map(render(header, 0, height, 'header'))}</View>

      {body.map((rows, rowIndex) => (
        <View style={{ ...defaultStyles.row, height: 25 }} key={rowIndex}>
          {rows.map(render(rows, rowIndex, height, 'body'))}
        </View>
      ))}

      {footer.length ? (
        <View style={defaultStyles.footer}>
          {footer.map(render(footer, height - 1, height, 'footer'))}
        </View>
      ) : null}
    </View>
  );
};

export { TablePDF };
