import React, { CSSProperties, FC } from 'react';

import { Text, View } from '@react-pdf/renderer';

import { TextProps } from 'models';

interface TextPropsComponent extends TextProps {
  value?: string;
}

const TextPDF: FC<TextPropsComponent> = props => {
  const { style, value } = props;
  return (
    <View style={style as CSSProperties}>
      <Text>{value}</Text>
    </View>
  );
};

export { TextPDF };
