import { useContext } from 'react';

import { FILE_STATUS, GuidType } from 'models';

import { FSContext } from 'state/context';

export function useFileSystem(folderId: GuidType) {
  const { fsItems, ...other } = useContext(FSContext);

  const items = other.getFolderElementsById(folderId);

  const defaultFolders = items.filter(item => item.isDefault);
  const progress = items.filter(item => item.status === FILE_STATUS.DOCUMENT_IN_PROGRESS);
  const body = items.filter(
    item => item.status !== FILE_STATUS.DOCUMENT_IN_PROGRESS && !item.isDefault,
  );

  return {
    items,
    elements: [defaultFolders, progress, body],
    ...other,
  };
}
