export type GuidType = string;
export type DateISO = string;

/*
  File system models -- start
 */

const FOLDER = 'FILE_SYSTEM_ELEMENT__FOLDER';
const DOCUMENT = 'FILE_SYSTEM_ELEMENT__DOCUMENT';

export const FILE_SYSTEM_ELEMENT = { FOLDER, DOCUMENT };

export type FileSystemElementType =
  | typeof FILE_SYSTEM_ELEMENT.FOLDER
  | typeof FILE_SYSTEM_ELEMENT.DOCUMENT;

export interface UserLog {
  userId?: GuidType;
  login: string;
  date: DateISO;
}

export interface FSTarget<T> {
  name?: string;
  value: string;
  item: T;
}

export interface FileSystemElement {
  id?: GuidType;
  parentId: GuidType | null;
  name: string;
  type: FileSystemElementType;
  status?: FileStatusType;
  formTemplate?: boolean;
  documentId?: GuidType;
  editor?: UserLog;
  creator?: UserLog;
  isDefault?: boolean;
}

export interface FileSystemNode extends FileSystemElement {
  children?: FileSystemNode[];
}

const DOCUMENT_IN_PROGRESS = 'FILE_STATUS__DOCUMENT_IN_PROGRESS';
const DOCUMENT__DONE = 'FILE_STATUS__DOCUMENT__DONE';
const TEMPLATE = 'FILE_STATUS__TEMPLATE';

export const FILE_STATUS = { DOCUMENT_IN_PROGRESS, DOCUMENT__DONE, TEMPLATE };

export type FileStatusType =
  | typeof FILE_STATUS.DOCUMENT_IN_PROGRESS
  | typeof FILE_STATUS.DOCUMENT__DONE
  | typeof FILE_STATUS.TEMPLATE;

/*
  File system models -- end
*/

/*
  Document models -- start
*/

const TEXT = 'ELEMENT__TEXT';
const BLOCK = 'ELEMENT__BLOCK';
const BORDER = 'ELEMENT__BORDER';
const IMAGE = 'ELEMENT__IMAGE';
const TABLE = 'ELEMENT__TABLE';

export const ELEMENT = { TEXT, BLOCK, BORDER, IMAGE, TABLE };

export type ElementType =
  | typeof ELEMENT.TEXT
  | typeof ELEMENT.BLOCK
  | typeof ELEMENT.BORDER
  | typeof ELEMENT.IMAGE
  | typeof ELEMENT.TABLE;

export interface Position {
  x: number;
  y: number;
}

export interface Size {
  height: number;
  width: number;
}

export interface Element {
  id: GuidType;
  type: ElementType;
  position: Position;
  size: Size;
}

export interface BaseStyle {
  width?: string;
  height?: string;
  borderWidth?: string;
  borderColor?: string;
  borderStyle?: string;
  border?: string;
  borderTop?: string;
  borderRight?: string;
  borderBottom?: string;
  borderLeft?: string;
  opacity?: string;
  backgroundColor?: string;
  borderBottomWidth?: string;
  borderBottomStyle?: string;
  borderBottomColor?: string;
  borderRightWidth?: string;
  borderRightStyle?: string;
  borderRightColor?: string;
}

export interface FontStyle {
  color?: string;
  fontFamily?: string;
  fontSize?: string;
  fontStyle?: string;
  fontWeight?: string;
  letterSpacing?: string;
  lineHeight?: string;
  textAlign?: string;
  textTransform?: string;
  textDecoration?: string;
}

export type ElementStyle = BaseStyle & FontStyle;

export interface CellStyle {
  colSpan?: number;
  // rowSpan?: number; TODO: implement in next version
  style?: ElementStyle;
}

export interface CellStyles {
  [id: string]: CellStyle;
}

export type CellType = 'header' | 'body' | 'footer' | 'pdf';

export interface CellProps {
  isActive?: boolean;
  isDone?: boolean;
  row: number;
  column: number;
  value: string;
  style: ElementStyle;
  changeTable: ChangeTable | undefined;
  actions?: {
    addColSpan: (row: number, column: number) => void;
    removeColumnFromTableElement: (column: number) => void;
  };
  cellType?: CellType;
}

export interface TableProps {
  data: string[][];
  hasFooter?: boolean;
  style?: ElementStyle;
  cellsStyle?: CellStyles;
}

export type TableElement = Element & TableProps;

export interface BlockProps {
  style?: BaseStyle;
}

export type BlockElement = Element & BlockProps;

export interface BorderProps {
  style?: BaseStyle;
}

export type BorderElement = Element & BorderProps;

export interface TextProps {
  value?: string;
  style?: ElementStyle;
}

export type TextElement = Element & TextProps;

export interface ImageProps {
  style?: BaseStyle;
  value?: string;
}

export type ImageElement = Element & ImageProps;

export type ElementUnion = TableElement | BlockElement | BorderElement | TextElement | ImageElement;

export interface Control {
  name: string;
  icon: string;
  type: ElementType;
}

export type DocumentPage = ElementUnion[];

export interface ElementControl {
  control: Control;
  element: ElementUnion;
}

export interface DragObject {
  type: string; // TODO: make type
  element: ElementType;
}

export type ChangeTable = (row: number, column: number, value: string) => void;

export type SerializedDocument = string;

export interface Document {
  id?: GuidType;
  templateId?: GuidType;
  name: string;
  content: SerializedDocument; // === JSON.stringify(DocumentPage[])
}

/*
  Document models -- end
*/

/*
  User models -- start
*/

const ADMIN = 'ROLE__ADMIN';
const MODERATOR = 'ROLE__MODERATOR';
const PARTICIPANT = 'ROLE__PARTICIPANT';

export const ROLE = { ADMIN, MODERATOR, PARTICIPANT };

export type RoleType = typeof ROLE.ADMIN | typeof ROLE.MODERATOR | typeof ROLE.PARTICIPANT;

export interface User {
  id: GuidType;
  login: string;
  role: RoleType;
}

/*
  User models -- end
*/
