import React, { FC, useContext, useState } from 'react';
import { RouteComponentProps, withRouter } from 'react-router';

import { Input, Modal as ModalAnt } from 'antd';

import { FILE_STATUS, FILE_SYSTEM_ELEMENT, FileSystemElement } from 'models';

import { MODAL_MODES, ModalModesType, useModalContext } from 'state';
import { FSContext } from 'state/context';

export interface ModalProps extends RouteComponentProps<{ id: string }> {}

const getTitle = (mode: ModalModesType): string => {
  if (mode === MODAL_MODES.CREATE_DOCUMENT) {
    return 'Create new document';
  }
  if (mode === MODAL_MODES.CREATE_FOLDER) {
    return 'Create new folder';
  }
  if (mode === MODAL_MODES.CREATE_DOCUMENT_FROM_TEMPLATE) {
    return 'Create document from template';
  }
  if (mode === MODAL_MODES.RENAME_FS_ITEM) {
    return 'Rename element';
  }

  throw new Error('Unknown mode: ' + mode);
};

const ModalComponent: FC<ModalProps> = props => {
  const [name, setName] = useState('');
  const { open, mode, modalToggle, target } = useModalContext();
  const { fsItems, createItem, rename, parentForCreateFromTemplate } = useContext(FSContext);

  const { match, history } = props;

  const onClose = () => {
    modalToggle();
    setName('');
  };
  const onCreate = async () => {
    const data: FileSystemElement = {
      name,
      parentId: match.params.id,
      type: FILE_SYSTEM_ELEMENT.FOLDER,
    };

    if (mode === MODAL_MODES.CREATE_DOCUMENT) {
      const parent: FileSystemElement | undefined = fsItems.get(match.params.id);
      if (!parent) return;

      const isTemplate = parent.name === 'Vorlage';
      data.type = FILE_SYSTEM_ELEMENT.DOCUMENT;
      data.status = isTemplate ? FILE_STATUS.TEMPLATE : FILE_STATUS.DOCUMENT_IN_PROGRESS;
    }

    if (mode === MODAL_MODES.CREATE_DOCUMENT_FROM_TEMPLATE) {
      if (!parentForCreateFromTemplate || !parentForCreateFromTemplate.id) return;

      data.parentId = parentForCreateFromTemplate.id;
      data.type = FILE_SYSTEM_ELEMENT.DOCUMENT;
      data.status = FILE_STATUS.DOCUMENT_IN_PROGRESS;
      data.formTemplate = true;
      data.documentId = (target as FileSystemElement).documentId;
    }

    await createItem(data);
    onClose();

    if (mode === MODAL_MODES.CREATE_DOCUMENT_FROM_TEMPLATE && parentForCreateFromTemplate) {
      history.push({ pathname: `${parentForCreateFromTemplate.id}` });
    }
  };

  const handleOk = async () => {
    try {
      if (mode === MODAL_MODES.RENAME_FS_ITEM) {
        await rename({ ...target, name });
        onClose();
      } else {
        onCreate();
      }
    } catch (error) {
      console.log('Error on create FS element', error);
    }
  };

  const handleInputChange = (event: any) => {
    setName(event.target.value);
  };

  const defaultValue =
    mode === MODAL_MODES.RENAME_FS_ITEM ? (target as FileSystemElement).name : '';

  return (
    <ModalAnt visible={open} title={getTitle(mode)} onOk={handleOk} onCancel={onClose}>
      {open && <Input autoFocus={true} onChange={handleInputChange} defaultValue={defaultValue} />}
    </ModalAnt>
  );
};

export const Modal = withRouter(ModalComponent);
