import React, { FC, useEffect, useRef, useState } from 'react';

import { DND_CONTROL_TYPES } from 'components/elementControl';
import { useDrop } from 'react-dnd';

import { Button, Tooltip } from 'antd';
import Box from './box';

import { EditorActions } from 'hooks/useEditor';
import { DocumentPage, DragObject, ELEMENT } from 'models';

import './styles.css';

export interface PageProps {
  pageIndex: number;
  page: DocumentPage;
  actions: EditorActions;
  isDone: boolean;
  preview: boolean;
}

const PageComponent: FC<PageProps> = props => {
  const { pageIndex, page, isDone, actions, preview } = props;

  // const [hasDropped, setHasDropped] = useState(false);
  // const [hasDroppedOnChild, setHasDroppedOnChild] = useState(false);
  const [bodyOffset, setBodyOffset] = useState(document.body.getBoundingClientRect());
  const [scrollY, setScrollY] = useState(bodyOffset.top);

  const listRef: any = useRef();

  const [obj, drop] = useDrop({
    accept: DND_CONTROL_TYPES.BOX,
    drop(item: DragObject, monitor) {
      const position = monitor.getClientOffset();

      if (!obj || !position) return;

      position.x = position.x - listRef.current.offsetLeft;
      position.y = position.y - listRef.current.offsetTop + scrollY;

      if (item.element === ELEMENT.IMAGE) {
        actions.addImage(pageIndex, item.element, position);
      } else {
        actions.addElement(pageIndex, item.element, position);
      }

      const didDrop = monitor.didDrop();

      if (didDrop) return;

      // setHasDropped(true);
      // setHasDroppedOnChild(didDrop);
    },
    collect: monitor => ({
      isOver: monitor.isOver(),
      isOverCurrent: monitor.isOver({ shallow: true }),
    }),
  });

  const listener = () => {
    setBodyOffset(document.body.getBoundingClientRect());
    setScrollY(-bodyOffset.top);
  };

  useEffect(() => {
    window.addEventListener('scroll', listener);
    return () => {
      window.removeEventListener('scroll', listener);
    };
  });

  return (
    <div
      className="list-container"
      ref={drop}
      onClick={() => {
        actions.resetActiveElement();
        actions.resetActiveId();
      }}
    >
      <div className={`list ${!preview ? 'greed-helper' : ''}`} ref={listRef}>
        {pageIndex !== 0 && !isDone && (
          <Tooltip className="remove-page" placement="right" title="delete">
            <Button
              type="danger"
              icon="close"
              size="small"
              shape="circle"
              onClick={() => actions.deletePage(pageIndex)}
            />
          </Tooltip>
        )}

        {page.map((element, index) => (
          <Box key={index} id={index} pageIndex={pageIndex} element={element} />
        ))}

        <span className="page">{pageIndex + 1}</span>
      </div>
    </div>
  );
};

export { PageComponent };
