import React, { FC, MutableRefObject, useRef } from 'react';
import { DraggableData, ResizableDelta, Rnd } from 'react-rnd';

import { Button, Tooltip } from 'antd';

import { BlockView, BorderView, ImageView, TableView, TextView } from 'components/elements';
import { TextSettings } from 'components/textSettings';

import { useEditorContext } from 'state/editor';

import { ELEMENT, ElementType, ElementUnion, Position, TableElement, TextElement } from 'models';

import './styles.css';

const renderElement = (
  element: ElementUnion,
  innerElement: MutableRefObject<HTMLDivElement | undefined>,
  changeTable: (row: number, column: number, value: string) => void,
  addColSpan: (row: number, column: number) => void,
  addRowToTableElement: () => void,
  addColumnToTableElement: () => void,
  removeRowFromTableElement: (row: number) => void,
  removeColumnFromTableElement: (column: number) => void,
  isActive: boolean,
  isDone: boolean,
  handleChangeText: (value: string) => void,
  preview: boolean,
) => {
  if (!element) return null;

  if (element.type === ELEMENT.BLOCK) {
    return <BlockView style={element.style} />;
  }

  if (element.type === ELEMENT.BORDER) {
    return <BorderView style={element.style} />;
  }

  if (element.type === ELEMENT.TABLE && 'hasFooter' in element) {
    const { data, hasFooter, style, cellsStyle } = element;

    return (
      <TableView
        innerRef={innerElement}
        data={data}
        hasFooter={hasFooter}
        style={style}
        cellsStyle={cellsStyle}
        isActive={isActive}
        isDone={isDone}
        changeTable={changeTable}
        preview={preview}
        actions={{
          addColSpan,
          addRowToTableElement,
          addColumnToTableElement,
          removeRowFromTableElement,
          removeColumnFromTableElement,
        }}
      />
    );
  }
  if (element.type === ELEMENT.IMAGE && 'value' in element) {
    return <ImageView style={element.style} value={element.value} />;
  }
  if (element.type === ELEMENT.TEXT && 'value' in element) {
    return (
      <TextView
        style={element.style}
        isActive={isActive}
        value={element.value}
        handleChangeText={handleChangeText}
      />
    );
  }
};

const getHeight = (
  innerElement: MutableRefObject<HTMLDivElement | undefined>,
  element: ElementUnion,
) => {
  if (element.type !== ELEMENT.TABLE || !innerElement.current) return element.size.height;

  const boundingRect = innerElement.current.getBoundingClientRect();
  return boundingRect.height;
};

const getEnableResizing = (type: ElementType, done: boolean) => {
  const enableResizing = {
    bottom: false,
    bottomLeft: false,
    bottomRight: false,
    right: false,
    left: false,
    top: false,
    topLeft: false,
    topRight: false,
  };

  if (type === ELEMENT.TABLE) {
    enableResizing.right = !done;
    return enableResizing;
  }

  if (done) return enableResizing;

  return undefined;
};

export interface BoxProps {
  id: number;
  pageIndex: number;
  element: ElementUnion;
}

const Box: FC<BoxProps> = props => {
  const { id, pageIndex, element } = props;
  const { isDone, activeElement, actions, preview } = useEditorContext();

  const isActiveText = activeElement && activeElement.type === ELEMENT.TEXT;

  const innerElement = useRef<HTMLDivElement>();

  const { position, size } = element;

  const stopPropagation = (e: any) => e.stopPropagation();

  const isActive = actions.isElementActive(element);
  const className = !isDone ? 'dnd-box' : '';

  const onDoubleClick = (e: any) => {
    if (preview) return;
    actions.setActiveElement(element);
    actions.setActiveId(pageIndex, id);
    actions.showDrawer();
  };

  const onDragStop = (e: any, data: DraggableData) => {
    element.position.x = data.x;
    element.position.y = data.y;

    actions.changeElement(pageIndex, id, element);
  };

  const onResizeStop = (
    e: any,
    dir: any,
    elementRef: HTMLDivElement,
    delta: ResizableDelta,
    // tslint:disable-next-line: no-shadowed-variable
    position: Position,
  ) => {
    const { width, height } = elementRef.getBoundingClientRect();

    element.position.x = position.x;
    element.position.y = position.y;
    element.size.width = width;
    element.size.height = height;
    actions.changeElement(pageIndex, id, element);
  };

  const changeTable = (row: number, column: number, value: string) => {
    (element as TableElement).data[row][column] = value;
    actions.changeElement(pageIndex, id, element);
  };

  const addRowToTableElement = () => {
    actions.addRowToTableElement(pageIndex, id, element as TableElement);
  };

  const addColumnToTableElement = () => {
    actions.addColumnToTableElement(pageIndex, id, element as TableElement);
  };

  const addColSpan = (row: number, column: number) => {
    actions.addColSpan(pageIndex, id, element as TableElement, row, column);
  };

  const removeRowFromTableElement = (row: number) => {
    actions.removeRowFromTableElement(pageIndex, id, element as TableElement, row);
  };

  const removeColumnFromTableElement = (column: number) => {
    actions.removeColumnFromTableElement(pageIndex, id, element as TableElement, column);
  };

  const handleChangeText = (value: string) => {
    actions.handleChangeText(pageIndex, id, value, element as TextElement);
  };

  const changeTextStyle = (style: string, value: string) => {
    actions.changeTextStyle(pageIndex, id, style, value, element as TextElement);
  };
  return (
    <Rnd
      className={className}
      bounds="parent"
      // default={{ ...position, ...size }}
      position={position}
      size={{ width: size.width, height: getHeight(innerElement, element) }}
      onClick={(e: any) => stopPropagation(e)}
      onDoubleClick={onDoubleClick}
      disableDragging={isActive || isDone || preview}
      onDragStop={onDragStop}
      enableResizing={getEnableResizing(element.type, isDone)}
      onResizeStop={onResizeStop}
    >
      {!isDone && isActive && (
        <Tooltip className="remove-btn" placement="right" title="delete">
          <Button
            type="danger"
            icon="close"
            size="small"
            shape="circle"
            onClick={() => actions.deleteElement(pageIndex, id)}
          />
        </Tooltip>
      )}
      {!isDone && isActive && isActiveText && (
        <TextSettings changeTextStyle={changeTextStyle} element={element} />
      )}
      {renderElement(
        element,
        innerElement,
        changeTable,
        addColSpan,
        addRowToTableElement,
        addColumnToTableElement,
        removeRowFromTableElement,
        removeColumnFromTableElement,
        isActive,
        isDone,
        handleChangeText,
        preview,
      )}
    </Rnd>
  );
};

export default Box;
