import React, { CSSProperties, FC } from 'react';

import { Image, View } from '@react-pdf/renderer';

import { ImageProps } from 'models';

interface ImagePropsComponent extends ImageProps {
  value?: string;
}

const ImagePDF: FC<ImagePropsComponent> = props => {
  const { style, value } = props;
  return (
    <View style={style as CSSProperties}>
      <Image src={value || ''} />
    </View>
  );
};

export { ImagePDF };
