import React, { FC, useContext, useEffect, useState } from 'react';

import { PDFDownloadLink } from '@react-pdf/renderer';
import { Icon, Menu } from 'antd';
import { connectMenu, ContextMenu, MenuItem } from 'react-contextmenu';

import { DocumentPDF } from 'components/documentPDF';

import { FSContext } from 'state/context';

import { Document, DocumentPage, FILE_STATUS, FileSystemElement } from 'models';

import { CONTEXT_MENU_ID } from '../explorer/contextMenu';
import './styles.css';

export interface DataItem {
  name: string;
  icon: string;
  value: string;
  target: string;
}

export interface MenuProps {
  data: DataItem[];
  currentId: string;
  onChange: any;
  trigger: {
    item: FileSystemElement;
  };
}

const ContextMenuComponent: FC<MenuProps> = props => {
  const { currentId, trigger, onChange } = props;

  let { data } = props;

  if (trigger && trigger.item) {
    if (trigger.item.status === FILE_STATUS.DOCUMENT_IN_PROGRESS) {
      data = data.filter(item => item.value !== 'edit-template');
    }

    if (trigger.item.status === FILE_STATUS.DOCUMENT__DONE) {
      data = data.filter(
        item => !['delete', 'commit', 'edit-template', 'rename'].includes(item.value),
      );
    }

    if (trigger.item.status === FILE_STATUS.TEMPLATE) {
      data = data.filter(item => !['commit', 'download'].includes(item.value));
    }
  }

  const [pages, setPages] = useState<DocumentPage[]>([]);
  const { documents } = useContext(FSContext);

  useEffect(() => {
    if (!trigger || !trigger.item) return;

    const unsubscribe = documents.doc(trigger.item.documentId).onSnapshot(querySnapshot => {
      const document = querySnapshot.data() as Document;
      const content: DocumentPage[] = JSON.parse(document.content);
      setPages(content);
    });

    return () => {
      unsubscribe();
    };
  }, [documents, trigger]);

  const fileName = trigger && trigger.item ? `${trigger.item.name}.pdf` : '';

  return (
    <ContextMenu id={currentId} className="context-menu">
      <Menu selectable={false}>
        {data.map((item: DataItem) => (
          <Menu.Item key={item.name}>
            <MenuItem key={item.name} data={item} onClick={onChange}>
              {item.value === 'download' ? (
                <PDFDownloadLink
                  key={item.name}
                  document={<DocumentPDF pages={pages} />}
                  fileName={fileName}
                >
                  {({ blob, url, loading, error }) =>
                    loading ? (
                      'Document loading...'
                    ) : (
                      <>
                        <Icon
                          type={item.icon}
                          className={item.value === 'delete' ? 'alert' : 'primary'}
                        />
                        <span>{item.name}</span>
                      </>
                    )
                  }
                </PDFDownloadLink>
              ) : (
                <>
                  <Icon
                    type={item.icon}
                    className={item.value === 'delete' ? 'alert' : 'primary'}
                  />
                  <span>{item.name}</span>
                </>
              )}
            </MenuItem>
          </Menu.Item>
        ))}
      </Menu>
    </ContextMenu>
  );
};

export const ContextMenuConnected = connectMenu(CONTEXT_MENU_ID.DOCUMENT)(ContextMenuComponent);
