import React, { FC, useContext } from 'react';

import { Link, RouteComponentProps } from 'react-router-dom';

import { Breadcrumb } from 'antd';

import { FSContext } from 'state/context';

import { Route } from 'antd/lib/breadcrumb/Breadcrumb';
import { FileSystemElement, GuidType } from 'models';

import './styles.css';

export interface BreadcrumbsProps extends RouteComponentProps<{ id: GuidType }> {}

const BreadcrumbComponent: FC<BreadcrumbsProps> = props => {
  const { match } = props;
  let { id } = match.params;

  id = id || 'Root';

  const { fsItems } = useContext(FSContext);

  return (
    <Breadcrumb
      className="breadcrumb"
      itemRender={itemRender}
      routes={getBreadcrumbList({ fsItems, id })}
    />
  );
};

interface ListProps {
  fsItems: Map<string, FileSystemElement>;
  id: GuidType;
}

function getBreadcrumbList(props: ListProps): Route[] {
  const list: Route[] = [];

  let item = props.fsItems.get(props.id);

  for (;;) {
    if (!item) {
      list.reverse();
      break;
    }

    const { id, name, parentId } = item;

    list.push({
      path: `/dashboard/${id}`,
      breadcrumbName: name,
      children: [],
    });

    item = props.fsItems.get(parentId as GuidType);
  }

  return list;
}

function itemRender(route: Route, params: any, routes: Route[]): React.ReactNode {
  const isLast = routes.indexOf(route) === routes.length - 1;

  return isLast ? (
    <span>{route.breadcrumbName}</span>
  ) : (
    <Link to={route.path}>{route.breadcrumbName}</Link>
  );
}

export { BreadcrumbComponent };
