import React, { FC, useState } from 'react';

import { Button, InputNumber } from 'antd';
import ChromePicker from 'react-color/lib/components/chrome/Chrome';

import { ELEMENT, ElementUnion } from 'models';
import '../styles.css';

export interface BlockProps {
  handleChangeSettings: (style: string, value: string, type: string) => void;
  activeElement: ElementUnion;
}

const popover = {
  position: 'absolute',
  zIndex: '2',
};
const cover = {
  position: 'fixed',
  top: '0px',
  right: '0px',
  bottom: '0px',
  left: '0px',
};

const setColor = (style: any): any => (style ? style : null);

const TableComponent: FC<BlockProps> = ({ handleChangeSettings, activeElement }) => {
  const { size, position, style, type } = activeElement;

  const [displayColorPicker, setDisplayColorPicker] = useState(false);
  const [openBorderColor, setBorderColor] = useState(false);

  const handleClick = () => {
    setDisplayColorPicker(!displayColorPicker);
  };

  const handleClose = () => {
    setDisplayColorPicker(false);
  };

  const handleClickBorder = () => {
    setBorderColor(!openBorderColor);
  };

  const handleCloseBorder = () => {
    setBorderColor(false);
  };

  const styleDefault = setColor(style);
  // @ts-ignore
  return (
    <div className="block-settings-editor">
      <div className="fields">
        <span>Width:</span>
        <InputNumber
          value={size.width}
          size="small"
          onChange={(value: any) => handleChangeSettings('width', value, 'size')}
        />
      </div>
      <div className="fields">
        <span>Height:</span>
        <InputNumber
          value={size.height}
          size="small"
          onChange={(value: any) => handleChangeSettings('height', value, 'size')}
        />
      </div>
      <div className="fields">
        <span>X:</span>
        <InputNumber
          value={position.x}
          size="small"
          onChange={(value: any) => handleChangeSettings('x', value, 'position')}
        />
      </div>
      <div className="fields">
        <span>Y:</span>
        <InputNumber
          value={position.y}
          size="small"
          onChange={(value: any) => handleChangeSettings('y', value, 'position')}
        />
      </div>
      <div className="fields">
        <span>Border:</span>
        <InputNumber
          value={styleDefault.borderWidth}
          size="small"
          onChange={(value: any) => handleChangeSettings('borderWidth', value, 'style')}
        />
      </div>
      <div>
        Background: <Button icon="bg-colors" className="icon-settings" onClick={handleClick} />
        {displayColorPicker ? (
          <div style={popover as any}>
            <div style={cover as any} onClick={handleClose} />
            <ChromePicker
              color={styleDefault.backgroundColor}
              onChangeComplete={(color: any) =>
                handleChangeSettings('backgroundColor', color.hex, 'style')
              }
            />
          </div>
        ) : null}
      </div>
      <div>
        Border Color:{' '}
        <Button icon="bg-colors" className="icon-settings" onClick={handleClickBorder} />
        {openBorderColor ? (
          <div style={popover as any}>
            <div style={cover as any} onClick={handleCloseBorder} />
            <ChromePicker
              color={styleDefault.backgroundColor}
              onChangeComplete={(color: any) =>
                handleChangeSettings('borderColor', color.hex, 'style')
              }
            />
          </div>
        ) : null}
      </div>
    </div>
  );
};

export default TableComponent;
