import React, { FC, useContext } from 'react';
import { RouteComponentProps, withRouter } from 'react-router';

import { DndProvider } from 'react-dnd';
import HTML5Backend from 'react-dnd-html5-backend';

import { Button, Drawer, Icon, Layout, Typography } from 'antd';

import { PDFDownloadLink } from '@react-pdf/renderer';

import { Breadcrumb } from 'components/breadcrumb';
import { DialogComponent } from 'components/dialog';
import { DocumentPDF } from 'components/documentPDF';
import { EditorComponent } from 'components/editor';
import { PageComponent } from 'components/page';

import { LeftPanel } from './components';

import { useModal } from 'hooks';
import { FSContext } from 'state/context';
import { useEditorContext } from 'state/editor';

import { FILE_STATUS, FileSystemElement, FSTarget } from 'models';

import './styles.css';

export interface DocumentProps extends RouteComponentProps<{ id: string }> {}

const getStatusLabel = (fsElement: FileSystemElement | null): string => {
  if (!fsElement) return '';

  if (fsElement.status === FILE_STATUS.TEMPLATE) {
    return 'Template';
  }
  if (fsElement.status === FILE_STATUS.DOCUMENT__DONE) {
    return 'Done';
  }
  if (fsElement.status === FILE_STATUS.DOCUMENT_IN_PROGRESS) {
    return 'In progress';
  }

  return '';
};

const DocumentPageComponentR: FC<DocumentProps> = props => {
  const {
    pages,
    isDone,
    showPreview,
    actions,
    drawerVisible,
    document,
    preview,
  } = useEditorContext();

  const isInProgress = document && document.status === FILE_STATUS.DOCUMENT_IN_PROGRESS;

  const [elementsModal, elementsModalActions] = useModal<FSTarget<FileSystemElement | null>>();
  const { commit, removeById } = useContext(FSContext);

  const handleAction = (value: 'commit' | 'delete') => () => {
    const title =
      value === 'commit' ? 'Are you sure you want to commit?' : 'Are you sure you want to delete?';

    elementsModalActions.setModalState({
      open: true,
      title,
      target: { value, item: document },
    });
  };

  const handleElementsModalAction = async () => {
    const { target } = elementsModal;

    if (!target || !target.item) return;

    if (target.value === 'commit') {
      await commit(target.item.id);
    }

    if (target.value === 'delete') {
      await removeById(target.item.id);
    }

    elementsModalActions.closeModal();

    if (target.value === 'delete') {
      props.history.goBack();
    }
  };

  return (
    <Layout className="document">
      <Layout.Header className="header">
        <Breadcrumb />

        <Button.Group>
          {!preview && !isDone && (
            <Button onClick={actions.addPage}>
              <Icon type="file" theme="filled" />
              Add page
            </Button>
          )}

          <Button onClick={actions.showPreviewHandler}>
            Preview
            <Icon type="file-done" />
          </Button>

          <PDFDownloadLink document={<DocumentPDF pages={pages} />} fileName="somename.pdf">
            {({ blob, url, loading, error }) =>
              loading ? (
                'Loading document...'
              ) : (
                <Button>
                  Download PDF
                  <Icon type="download" />
                </Button>
              )
            }
          </PDFDownloadLink>

          <Button onClick={actions.togglePreview}>
            Print
            <Icon type="printer" theme="filled" />
          </Button>

          {!isDone && (
            <Button onClick={handleAction('delete')}>
              Delete
              <Icon type="delete" />
            </Button>
          )}

          {isInProgress && (
            <Button onClick={handleAction('commit')}>
              Commit
              <Icon type="file-done" />
            </Button>
          )}
        </Button.Group>

        <Typography.Title level={4} code={true}>
          {getStatusLabel(document)}
        </Typography.Title>
      </Layout.Header>

      <Layout.Content className="content">
        <DndProvider backend={HTML5Backend}>
          {!isDone && (
            <LeftPanel
              pages={pages}
              showPreview={showPreview}
              togglePreview={actions.togglePreview}
            />
          )}

          <div className="pages">
            {pages.map((page, index: number) => (
              <PageComponent
                key={index}
                pageIndex={index}
                page={page}
                actions={actions}
                isDone={isDone}
                preview={preview}
              />
            ))}
          </div>
          <Button type="primary" onClick={actions.showDrawer}>
            Settings
          </Button>
          <Drawer
            title="Settings"
            width={720}
            visible={drawerVisible}
            onClose={actions.closeDrawer}
          >
            <EditorComponent />
          </Drawer>
        </DndProvider>
      </Layout.Content>

      <Layout.Footer className="footer">Binale Software ©2019</Layout.Footer>

      <DialogComponent
        open={elementsModal.open}
        title={elementsModal.title}
        content={elementsModal.content}
        onAction={handleElementsModalAction}
        onClose={elementsModalActions.closeModal}
      />
    </Layout>
  );
};

export const DocumentPageComponent = withRouter(DocumentPageComponentR);
