import React from 'react';

import { CollectionReference, DocumentReference } from '@firebase/firestore-types';
import { firestore } from 'config/firebase';

import {
  Document,
  FILE_STATUS,
  FILE_SYSTEM_ELEMENT,
  FileSystemElement,
  FileSystemElementType,
  FileSystemNode,
} from 'models';

interface Company {
  uuid: string;
}

interface Props {
  activeCompany?: Company;
}

interface State {
  fsItems: Map<string, FileSystemElement>;
  parentForCreateFromTemplate: FileSystemElement | null;
  copiedElement: FileSystemElement | null;
}

interface FSContextInterface {
  fsItems: Map<string, FileSystemElement>;
  documents: CollectionReference;
  copiedElement: FileSystemElement | null;
  parentForCreateFromTemplate: FileSystemElement | null;
  setParentForCreateFromTemplate: (parent: FileSystemElement) => void;
  getFolderElementsTreeById: (id: string, type?: FileSystemElementType) => FileSystemNode[];
  getFolderElementsById: (id: string, type?: FileSystemElementType) => FileSystemElement[];
  getElement: (id?: string) => FileSystemElement | null;
  commit: (id?: string) => Promise<void>;
  rename: (data: FileSystemElement) => Promise<void>;
  removeById: (id?: string) => Promise<void>;
  createItem: (data: FileSystemElement) => Promise<DocumentReference>;
  copyItem: (copyData: FileSystemElement) => void;
  pasteItem: (parentId: string) => Promise<DocumentReference | void>;
}
const mockFn: any = () => {};

const documents = firestore.collection('documents');

export const FSContext = React.createContext<FSContextInterface>({
  fsItems: new Map<string, FileSystemElement>(),
  documents,
  parentForCreateFromTemplate: null,
  copiedElement: null,
  setParentForCreateFromTemplate: mockFn,
  getFolderElementsTreeById: mockFn,
  getFolderElementsById: mockFn,
  getElement: mockFn,
  commit: mockFn,
  rename: mockFn,
  removeById: mockFn,
  createItem: mockFn,
  copyItem: mockFn,
  pasteItem: mockFn,
});

export class FSProvider extends React.PureComponent<Props, State> {
  state: State = {
    fsItems: new Map<string, FileSystemElement>(),
    parentForCreateFromTemplate: null,
    copiedElement: null,
  };

  collection = firestore.collection('folders');

  componentDidMount() {
    this.subscribe();
  }

  componentWillReceiveProps(nextProps: Readonly<Props>) {
    this.subscribe();
  }

  componentWillUnmount() {
    this.unsubscribe();
  }

  render() {
    const { fsItems, copiedElement, parentForCreateFromTemplate } = this.state;

    return (
      <FSContext.Provider
        value={{
          fsItems,
          documents,
          copiedElement,
          parentForCreateFromTemplate,
          setParentForCreateFromTemplate: this.setParentForCreateFromTemplate,
          getFolderElementsTreeById: this.getFolderElementsTreeById,
          getFolderElementsById: this.getFolderElementsById,
          getElement: this.getElement,
          commit: this.commit,
          rename: this.rename,
          copyItem: this.copyItem,
          createItem: this.createItem,
          pasteItem: this.pasteItem,
          removeById: this.removeById,
        }}
      >
        {this.props.children}
      </FSContext.Provider>
    );
  }

  protected subscribe() {
    this.unsubscribe();
    this.unsubscribe = this.collection.onSnapshot(querySnapshot => {
      const map = new Map<string, FileSystemElement>();
      // todo consider docChanges
      // https://firebase.google.com/docs/firestore/query-data/listen#view_changes_between_snapshots
      querySnapshot.forEach(doc => {
        const data: FileSystemElement = doc.data() as any;
        data.id = doc.id;
        map.set(doc.id, data);
      });
      this.setState({ fsItems: map });
    });
  }

  protected unsubscribe: () => void = () => {};

  protected getFolderElementsTreeById = (
    id: string,
    type?: FileSystemElementType,
  ): FileSystemNode[] => {
    const elements = this.getFolderElementsById(id, type);

    return elements.map<FileSystemNode>(element => {
      if (!element.id) return element;
      const children = this.getFolderElementsTreeById(element.id, type);
      return children.length > 0 ? { ...element, children } : element;
    });
  };
  protected getFolderElementsById = (
    id: string,
    type?: FileSystemElementType,
  ): FileSystemElement[] => {
    const items: FileSystemElement[] = [];

    this.state.fsItems.forEach(value => {
      const isNeededType = !type || (type && value.type === type);
      if (value.parentId === id && isNeededType) items.push({ ...value });
    });

    return items;
  };
  protected getElement = (id?: string): FileSystemElement | null => {
    if (!id) return null;

    const element = this.state.fsItems.get(id);

    return element || null;
  };
  protected commit = (id?: string): Promise<void> => {
    return this.collection.doc(id).update({ status: FILE_STATUS.DOCUMENT__DONE });
  };
  protected rename = (data: FileSystemElement): Promise<void> => {
    return this.collection.doc(data.id).update({ name: data.name });
  };
  protected removeById = (id?: string) => {
    return this.collection.doc(id).delete();
  };
  protected createItem = async (data: FileSystemElement): Promise<DocumentReference> => {
    try {
      if (data.type === FILE_SYSTEM_ELEMENT.DOCUMENT) {
        let newDocument: Document = {
          name: data.name,
          content: '[[]]',
        };

        if (data.formTemplate) {
          const templateSnapshot = await documents.doc(data.documentId).get();
          newDocument = templateSnapshot.data() as Document;
          newDocument.name = data.name;
        }

        const docRef = await documents.add(newDocument);
        data.documentId = docRef.id;
      }

      return await this.collection.add(data);
    } catch (error) {
      throw Error('File creation error ' + error);
    }
  };
  protected copyItem = (data: FileSystemElement) => {
    this.setState({ copiedElement: data });
  };
  protected pasteItem = async (parentId: string): Promise<DocumentReference | void> => {
    const { copiedElement } = this.state;
    if (!copiedElement) return;

    const newElement: FileSystemElement = {
      ...copiedElement,
      parentId,
    };

    if (copiedElement.parentId === parentId) {
      newElement.name = `copy_${newElement.name}`;
    }

    if (newElement.type === FILE_SYSTEM_ELEMENT.DOCUMENT) {
      if (newElement.status === FILE_STATUS.DOCUMENT__DONE) {
        newElement.status = FILE_STATUS.DOCUMENT_IN_PROGRESS;
      }

      newElement.formTemplate = true;
    }

    const docRef = await this.createItem(newElement);

    this.setState({ copiedElement: null });

    return docRef;
  };
  protected setParentForCreateFromTemplate = (parent: FileSystemElement) => {
    this.setState({ parentForCreateFromTemplate: parent });
  };
}
