import React, { FC } from 'react';

import { CellProps, CellStyle, CellStyles, CellType, ChangeTable, ElementStyle } from 'models';

export interface TableMembers {
  header: string[];
  body: string[][];
  footer: string[];
}

export interface Count {
  current: number;
  length: number;
}

export interface CellPosition {
  row: number;
  column: number;
}

const CELL_POSITION_SPLITTER = '-';

const DEFAULT_PROPS = {
  FONT_SIZE: 12,
  HEADER_BG_COLOR: '#ccc',
  BODY_BG_COLOR: '#fff',
  FOOTER_BG_COLOR: '#ccc',
  CELL_PADDING: '2pt 6pt',
  BORDER_WIDTH: '1pt',
  BORDER_COLOR: '#000',
  BORDER_STYLE: 'solid',
};

const DEFAULT_BORDER = `${DEFAULT_PROPS.BORDER_WIDTH} ${DEFAULT_PROPS.BORDER_STYLE} ${
  DEFAULT_PROPS.BORDER_COLOR
}`;

export const getCellPositionFromId = (id: string): CellPosition => {
  const [row, column] = id.split(CELL_POSITION_SPLITTER);
  return { row: +row, column: +column };
};

export const getIdFromCellPosition = ({ row, column }: CellPosition): string =>
  `${row}${CELL_POSITION_SPLITTER}${column}`;

const getCellStyle = (row: number, column: number, styles: CellStyles): CellStyle => {
  const id = getIdFromCellPosition({ row, column });
  const style = styles[id];
  return style || {};
};

const getBorderStyle = (row: Count, column: Count, style?: string): ElementStyle => {
  style = style || DEFAULT_BORDER;
  const border: ElementStyle = {};

  const isLastRow = row.current + 1 === row.length;
  const isLastColumn = column.current + 1 === column.length;

  if (!isLastRow) border.borderBottom = style;
  if (!isLastColumn) border.borderRight = style;

  return border;
};

export const baseTableStyles = {
  table: {
    display: 'flex',
    flexDirection: 'column',
    border: DEFAULT_BORDER,
    backgroundColor: DEFAULT_PROPS.BODY_BG_COLOR,
    fontSize: DEFAULT_PROPS.FONT_SIZE,
  },
  header: {
    display: 'flex',
    flexDirection: 'row',
    backgroundColor: DEFAULT_PROPS.HEADER_BG_COLOR,
    fontWeight: 'bold',
  },
  footer: {
    display: 'flex',
    flexDirection: 'row',
    backgroundColor: DEFAULT_PROPS.FOOTER_BG_COLOR,
  },
  row: {
    display: 'flex',
    flexDirection: 'row',
  },
  cell: {
    display: 'flex',
    padding: DEFAULT_PROPS.CELL_PADDING,
  },
};

export const getTableMembers = (data: string[][], hasFooter: boolean = false): TableMembers => {
  const [header, ...other] = data;
  const footer: string[] = hasFooter ? other.pop() || [] : [];

  return { header, body: other, footer };
};

export const cellRender = (
  defaultColumnWidth: number,
  defaultCellStyle: ElementStyle,
  cellsStyle: CellStyles,
  CellComponent: FC<CellProps>,
  changeTable?: ChangeTable,
  isActive?: boolean,
  isDone?: boolean,
  actions?: {
    addColSpan: (row: number, column: number) => void;
    removeColumnFromTableElement: (column: number) => void;
  },
) => (data: string[], rowIndex: number, rowsLength: number, cellType: CellType) => (
  cell: string,
  cellIndex: number,
) => {
  const rowCount: Count = { current: rowIndex, length: rowsLength };
  const columnCount: Count = { current: cellIndex, length: data.length };

  const { colSpan = 1, style = {} } = getCellStyle(
    rowIndex + (cellType === 'body' ? 1 : 0),
    cellIndex,
    cellsStyle,
  );

  const { borderWidth, borderStyle, borderColor, ...defaultStyle } = defaultCellStyle;

  let defaultBorderStyle;
  if (borderWidth && borderStyle && borderColor) {
    defaultBorderStyle = `${borderWidth} ${borderStyle} ${borderColor}`;
  }

  const resultStyle = {
    width: `${defaultColumnWidth * colSpan}%`,
    ...defaultStyle,
    ...getBorderStyle(rowCount, columnCount, defaultBorderStyle),
    ...style,
  };

  const cellComponent: JSX.Element = (
    <CellComponent
      key={cellIndex}
      style={resultStyle}
      value={cell}
      isActive={isActive}
      isDone={isDone}
      row={rowCount.current + (cellType === 'body' ? 1 : 0)}
      column={columnCount.current}
      cellType={cellType}
      changeTable={changeTable}
      actions={actions}
    />
  );

  return cellComponent;
};
