import React, { FC } from 'react';

import { Modal } from 'antd';

export interface DialogProps {
  open: boolean;
  width?: string | number;
  bodyStyle?: any;
  title?: string;
  content: any;
  footer?: React.ReactNode | null;
  onClose?: (e: React.MouseEvent<HTMLElement>) => void;
  onAction?: (e: React.MouseEvent<HTMLElement>) => void;
}

const DialogComponent: FC<DialogProps> = props => {
  const { open, title, content, width, bodyStyle, onAction, onClose, footer } = props;

  return (
    <Modal
      width={width}
      bodyStyle={bodyStyle}
      visible={open}
      title={title}
      onOk={onAction}
      onCancel={onClose}
      footer={footer}
    >
      {content}
    </Modal>
  );
};

export { DialogComponent };
