import React, { CSSProperties, FC, useEffect, useRef, useState } from 'react';

import { Button, Form, Icon, Input, Tooltip } from 'antd';

import { CellProps, ChangeTable, TableProps } from 'models';

import { baseTableStyles, cellRender, getTableMembers } from './helpers';

import { useEditorContext } from 'state/editor';
import './styles.css';

interface TableViewProps extends TableProps {
  innerRef: any;
  changeTable: ChangeTable;
  actions: {
    addColSpan: (row: number, column: number) => void;
    addRowToTableElement: () => void;
    addColumnToTableElement: () => void;
    removeRowFromTableElement: (row: number) => void;
    removeColumnFromTableElement: (column: number) => void;
  };
  isActive: boolean;
  isDone: boolean;
  preview: boolean;
}

const defaultStyles: any = baseTableStyles;

const TableCell: FC<CellProps> = props => {
  const { preview } = useEditorContext();
  const { value, style, isActive, isDone, row, column, changeTable, cellType } = props;
  const [isEditable, setIsEditable] = useState(false);
  const toggleEditability = () => setIsEditable(!isEditable);

  const inputRef = useRef<HTMLInputElement>();

  useEffect(() => {
    if (isEditable && inputRef && inputRef.current) {
      inputRef.current.focus();
    }
  }, [isEditable]);

  const onClick = () => {
    if (isActive && !isDone) toggleEditability();
  };

  const save = (ev: any) => {
    if (changeTable) changeTable(row, column, ev.target.value);
    toggleEditability();
  };

  const className = cellType === 'header' ? 'cell header-cell' : 'cell';

  const removeColumn = () => {
    if (!props.actions) return;
    props.actions.removeColumnFromTableElement(column);
  };

  const addColSpan = () => {
    if (!props.actions) return;
    props.actions.addColSpan(row, column);
  };

  return isEditable ? (
    <div className={className} style={style as CSSProperties}>
      <Form.Item className="cell-input">
        <Input.TextArea
          ref={inputRef as any}
          autosize={true}
          defaultValue={value}
          onPressEnter={save}
          onBlur={save}
        />
      </Form.Item>
    </div>
  ) : (
    <div className={className} style={style as CSSProperties} onClick={onClick}>
      {cellType === 'header' && !preview && !isDone && (
        <div className="header-actions">
          <Tooltip placement="top" title="delete column">
            <Button type="danger" icon="close" size="small" shape="circle" onClick={removeColumn} />
          </Tooltip>
        </div>
      )}
      {!isDone && (
        <div className="cell-actions">
          <Tooltip placement="top" title="combine with next cell">
            <Icon type="right-circle" theme="filled" onClick={addColSpan} />
          </Tooltip>
        </div>
      )}
      <span>{value}</span>
    </div>
  );
};

const TableView: FC<TableViewProps> = props => {
  const {
    data,
    hasFooter = false,
    style = {},
    cellsStyle = {},
    innerRef,
    changeTable,
    actions,
    isActive,
    isDone,
    preview,
  } = props;

  const { header, body, footer } = getTableMembers(data, hasFooter);

  const defaultColumnWidth = 100 / header.length;
  const height = data.length;
  const render = cellRender(
    defaultColumnWidth,
    {
      ...defaultStyles.cell,
      ...{
        backgroundColor: style.backgroundColor,
        borderWidth: style.borderWidth,
        borderStyle: style.borderStyle,
        borderColor: style.borderColor,
      },
    },
    cellsStyle,
    TableCell,
    changeTable,
    isActive,
    isDone,
    actions,
  );

  return (
    <>
      {!preview && !isDone && (
        <div className="table-element-controls">
          <Tooltip placement="right" title="add row">
            <Button
              icon="arrow-down"
              size="small"
              shape="circle"
              onClick={actions.addRowToTableElement}
            />
          </Tooltip>
          <Tooltip placement="right" title="add column">
            <Button
              icon="arrow-right"
              size="small"
              shape="circle"
              onClick={actions.addColumnToTableElement}
            />
          </Tooltip>
        </div>
      )}

      <div ref={innerRef} className="table-element" style={{ ...defaultStyles.table, ...style }}>
        <div style={defaultStyles.header}>{header.map(render(header, 0, height, 'header'))}</div>

        {body.map((rows, rowIndex) => (
          <div className="row" style={defaultStyles.row} key={rowIndex}>
            {!preview && !isDone && (
              <div className="row-actions">
                <Tooltip placement="left" title="delete row">
                  <Button
                    type="danger"
                    icon="close"
                    size="small"
                    shape="circle"
                    onClick={() => actions.removeRowFromTableElement(rowIndex)}
                  />
                </Tooltip>
              </div>
            )}
            {rows.map(render(rows, rowIndex, height, 'body'))}
          </div>
        ))}

        {footer.length ? (
          <div style={defaultStyles.footer}>
            {footer.map(render(footer, height - 1, height, 'footer'))}
          </div>
        ) : null}
      </div>
    </>
  );
};

export { TableView };
